.workshop {
    background: url('../assets/images/workshop_bg.jpg') repeat  right 45% top fixed;
    background-size: cover;
    overflow-x: hidden;
    transform: translateX(0);
    @include animate( all, 250ms );

    &::before {
        background-image: linear-gradient(to right, rgba(0, 0, 0, .80),rgba(0, 0, 0, .20) 50%);
        content: '';
        inset: 0;
        position: absolute;
    }

    .workshop-content {
        margin-top: 2rem;
        transform: translateX(0);
        @include animate( all, 250ms );

        h1 {
            color: white;
            font-size: 2.4rem;
            font-weight: 400;
            margin: 0 0 1rem;
            text-shadow: 1px 1px 1px black;
        }

        nav.page-links {
            align-items: center;
            display: flex;
            flex-wrap: wrap;
            justify-content: flex-start;
            margin: 0 0 2rem;

            a {
                color: $gl__light;
                font-size: 1.1rem;
                font-weight: 600;
                margin: .5rem 1.5rem .5rem 0;
                text-decoration: none;
                text-shadow: 1px 1px 1px black;
                text-transform: uppercase;
                white-space: nowrap;
                @include animate( color, 250ms );

                &:active, &:hover {
                    color: $gl__dark;
                }
            }
        }

        p {
            color: $gl__white;
            font-size: 1.2rem;
            letter-spacing: .05rem;
            margin: 0 0 1.5rem;
            text-shadow: 1px 1px 1px black;

            em {
                font-style: italic;
            }
        }

        ul {
            color: $gl__white;
            font-size: 1.2rem;
            letter-spacing: .05rem;
            text-shadow: 1px 1px 1px black;

            li {
                margin-bottom: 1rem;
            }
        }

        a {
            color: $gl__light;
            font-size: 1.2rem;
            font-weight: 600;
            margin: .5rem 0 .5rem 0;
            text-decoration: none;
            text-shadow: 1px 1px 1px black;
            white-space: nowrap;
            @include animate( color, 250ms );

            &:active, &:hover {
                color: $gl__dark;
            }
        }

        .course-content {
            align-items: flex-start;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;

            article {
                flex: 0 100%;
                margin-bottom: 3rem;

                h1 {
                    margin: 0 0 0 0;
                }
                h3 {
                    color: $gl__dark;
                    font-size: 1.4rem;
                    margin: 0 0 1.5rem 0;
                    text-shadow: 1px 1px 1px black;
                }

                a {
                    text-transform: uppercase;
                }

                &.schedule, &.benefit-value {
                    h3 {
                        height: 0;
                    }
                    
                    ul {
                        list-style: none;
                        
                        li {
                            align-items: flex-start;
                            display: flex;
                            justify-content: space-between;
                            margin-bottom: .5rem;
                            position: relative;

                            &::before {
                                content: "";
                                left: -2rem;
                                position: absolute;
                            }

                            em {
                                display: block;
                                font-size: 80%;
                            }

                            span:nth-child(2) {
                                font-weight: 700;
                            }
                        }
                    }
                }

                &.benefit-value {
                    p {
                        align-items: flex-start;
                        display: flex;
                        justify-content: space-between;

                        &.benefits-title, &.gl-total {
                            font-weight: 700;
                        }
                    }

                    ul {
                        list-style: none;
                        margin: -1rem 0 2rem 0;
                    }
                }
            }
        }

        p.footer-info {
            color: $gl__grey;
            font-size: 1.2rem;
            padding-bottom: 2rem;
            text-align: left;
            text-decoration: none;
            text-shadow: 1px 1px 2px black, 0 0 0.2em black;
        }

        &.hide {
            display: none;
        }
        
        &.right {
            transform: translateX(100vw);
        }
        &.left {
            transform: translateX(-100vw);
        }

    }

    &.hide {
        display: none;
    }
    
    &.right {
        transform: translateX(100vw);
    }
    &.left {
        transform: translateX(-100vw);
    }

}

